.work-experience {
  text-align: left;
  max-width: 1500px;  /* Set a reasonable max-width for large screens */
  margin: 0 auto;  /* Center the content */
  padding-left: 1rem;  /* Default padding on the left */
  padding-right: 3rem;  /* Padding on the right */
  padding-top: 3rem;  /* Add space at the top of the container */
  font-family: Arial, sans-serif;
  color: #333;
}

/* Adjust padding on larger screens */
@media (min-width: 1200px) {
  .work-experience {
    padding-right: 30%;  /* Increase right padding on larger screens */
  }
}

h1 {
  text-align: left;
  margin-bottom: 0px;
  font-size: 2rem;  /* Changed from em to rem */
  color: #333;
}

.job-container {
  padding-top: 2vh;
  padding-bottom: 2vh;
  border-bottom: 1px solid #ddd;
}

h2 {
  font-size: 1.5rem;  /* Changed from em to rem */
  margin-bottom: 1vh;
  color: #222;
}

.job-details {
  margin-top: 0px;
  margin-bottom: 3vh;
  font-size: 1rem;  /* Changed to rem for consistency */
  line-height: 1.8;
}

.job-details strong {
  font-weight: 600;
}

/* Responsive adjustments */
@media (max-width: 768px) {
  h1 {
    font-size: 1.5rem; /* Smaller font size for smaller screens */
  }

  h2 {
    font-size: 1.25rem; /* Adjust font size for smaller screens */
  }

  .job-details {
    font-size: 0.875rem; /* Adjust content font size for better readability on small screens */
  }

  .work-experience {
    padding-left: 0.5rem;
    padding-right: 1rem;  /* Reduce padding for smaller screens */
  }
}
